// src/config/constants.js

// ----- App info -----
export const APP_CONFIG = {
  name: import.meta.env.VITE_APP_NAME || 'SmartResumeTool',
  version: import.meta.env.VITE_APP_VERSION || '2.0.0',
};

// ----- File handling -----
const rawFormats = (import.meta.env.VITE_SUPPORTED_FORMATS || '.pdf,.doc,.docx,.txt')
  .split(',')
  .map(s => s.trim().toLowerCase())
  .filter(Boolean);

// normalize to ensure leading dot
const normalizedFormats = rawFormats.map(ext => (ext.startsWith('.') ? ext : `.${ext}`));

export const FILE_CONFIG = {
  maxSize:
    Number(import.meta.env.VITE_MAX_FILE_SIZE) > 0
      ? Number(import.meta.env.VITE_MAX_FILE_SIZE)
      : 5 * 1024 * 1024, // 5 MB default
  supportedFormats: normalizedFormats,
};

// ----- Claude (proxy-only) -----
// IMPORTANT: The browser NEVER holds the API key. It only calls your Cloudflare Worker.
// Set VITE_API_BASE_URL in .env to your Worker URL, e.g.:
// VITE_API_BASE_URL=https://smart-resume-proxy.davidmadison95.workers.dev
export const CLAUDE_CONFIG = {
  baseURL: import.meta.env.VITE_API_BASE_URL || '', // fallback to relative when developing locally
  model: import.meta.env.VITE_CLAUDE_MODEL || 'claude-3-5-sonnet-20241022',
  maxTokens: 4096,
  temperature: 0.3,
};

// ----- UX/Error text -----
export const ERROR_MESSAGES = {
  FILE_TOO_LARGE: 'File is too large.',
  FILE_TYPE_NOT_ALLOWED: 'Unsupported file type.',
  FILE_READ_ERROR: 'Could not read the file.',
  PARSE_ERROR: 'Unable to extract text from the file.',
  NETWORK_ERROR: 'Network error. Please try again.',
  API_ERROR: 'AI analysis is unavailable. Using traditional analysis instead.',
};

// ----- Analysis controls -----
export const ANALYSIS_CONFIG = {
  maxKeywords: 150,
  stopWords: new Set([
    'the','and','for','with','from','that','this','you','your','are','was','were',
    'have','has','had','a','an','to','of','in','on','as','by','at','it','be','or'
  ]),
};

export const SCORING_WEIGHTS = {
  KEYWORD_MATCH: 40,
  ATS_FORMAT: 25,
  CONTENT_STRUCTURE: 20,
  CONTACT_INFO: 10,
  MEASURABLE_RESULTS: 5,
};
