/**
 * Resume Analysis Service
 * Combines traditional algorithmic analysis with optional AI insights
 */

import { claudeAPI } from './claudeAPI';
import { ANALYSIS_CONFIG, SCORING_WEIGHTS } from '@config/constants';

class AnalysisService {
  /**
   * Perform complete resume analysis
   * @param {string} resumeText
   * @param {string} jobDescription
   * @param {boolean} useAI
   */
  async analyzeResume(resumeText, jobDescription, useAI = true) {
    // Basic input validation with friendly errors for the UI
    if (!resumeText || resumeText.trim().length < 50) {
      throw new Error('Your resume text looks too short to analyze. Please upload a full resume (PDF/DOC/TXT).');
    }
    if (!jobDescription || jobDescription.trim().length < 50) {
      throw new Error('Please paste a complete job description (at least a few sentences).');
    }

    // Always compute the local/traditional analysis first (fast + offline-safe)
    const traditional = this.performTraditionalAnalysis(resumeText, jobDescription);

    // Optionally enrich with AI if configured; never fail the whole run on AI errors
    let ai = null;
    if (useAI && claudeAPI?.isConfigured?.()) {
      try {
        ai = await claudeAPI.analyzeResume(resumeText, jobDescription);
      } catch (err) {
        // Log only; the UI already shows a small banner that AI is unavailable
        console.error('AI analysis failed, continuing with traditional analysis:', err);
      }
    }

    return this.mergeAnalyses(traditional, ai);
  }

  /**
   * Traditional algorithmic analysis (always works, no API needed)
   */
  performTraditionalAnalysis(resumeText, jobDescription) {
    const resumeKeywords = this.extractKeywords(resumeText);
    const jobKeywords = this.extractKeywords(jobDescription);

    // matches / missing (case-insensitive, unique)
    const lower = (s) => s.toLowerCase();
    const uniq = (arr) => [...new Set(arr)];
    const resumeSet = new Set(resumeKeywords.map(lower));
    const jobSet = uniq(jobKeywords.map(lower));

    const matchedKeywords = jobSet.filter((kw) => resumeSet.has(kw));
    const missingKeywords = jobSet.filter((kw) => !resumeSet.has(kw));

    const keywordMatchScore =
      jobSet.length > 0 ? Math.round((matchedKeywords.length / jobSet.length) * 100) : 0;

    const atsScore = this.calculateATSScore(resumeText, matchedKeywords.length, jobSet.length);

    const recommendations = this.generateRecommendations(resumeText, missingKeywords, atsScore);
    const insights = this.generateInsights(resumeText, matchedKeywords, missingKeywords);
    const metadata = this.extractMetadata(resumeText);

    return {
      scores: {
        overall: atsScore,
        keywordMatch: keywordMatchScore,
        breakdown: this.calculateScoreBreakdown(resumeText, matchedKeywords, jobSet),
      },
      keywords: {
        matched: matchedKeywords,
        missing: uniq(missingKeywords).slice(0, 15), // cap list
        total: jobSet.length,
      },
      recommendations,
      insights,
      metadata,
      analysisType: 'traditional',
      aiEnhanced: false,
    };
  }

  /**
   * Extract keywords & short phrases
   */
  extractKeywords(text) {
    if (!text || text.length < 10) return [];

    // normalize casing
    let t = text.toLowerCase();

    // normalize common tech terms
    const map = {
      'react.js': 'reactjs',
      'node.js': 'nodejs',
      'vue.js': 'vuejs',
      'c++': 'cplusplus',
      'c#': 'csharp',
      '.net': 'dotnet',
    };
    for (const [raw, norm] of Object.entries(map)) {
      t = t.replace(new RegExp(raw, 'gi'), norm);
    }

    // keep letters, digits, +, #, ., - to preserve tech tokens
    const tokens = t
      .replace(/[^\w\s+#.-]/g, ' ')
      .split(/\s+/)
      .filter((w) => w.length > 2 && !ANALYSIS_CONFIG.stopWords.has(w));

    // frequency count
    const freq = new Map();
    for (const w of tokens) freq.set(w, (freq.get(w) || 0) + 1);

    // add known multiword phrases if present
    for (const phrase of this.extractPhrases(t)) {
      freq.set(phrase, (freq.get(phrase) || 0) + 1);
    }

    // top-N
    return [...freq.entries()]
      .sort((a, b) => b[1] - a[1])
      .slice(0, ANALYSIS_CONFIG.maxKeywords)
      .map(([term]) => term);
  }

  extractPhrases(text) {
    const phrases = [];
    const technicalPhrases = [
      'machine learning',
      'data analysis',
      'project management',
      'full stack',
      'front end',
      'back end',
      'software development',
      'agile methodology',
      'version control',
      'database management',
      'api development',
      'cloud computing',
      'data visualization',
      'business intelligence',
      'quality assurance',
      'user experience',
      'customer service',
      'team leadership',
    ];
    for (const p of technicalPhrases) if (text.includes(p)) phrases.push(p);
    return phrases;
  }

  calculateATSScore(resumeText, matchedCount, totalKeywords) {
    let score = 0;

    // Keyword match (weight applies 0100 scale)
    if (totalKeywords > 0) {
      score += (matchedCount / totalKeywords) * SCORING_WEIGHTS.KEYWORD_MATCH;
    }

    // Format quality
    const formatScore = this.assessFormat(resumeText);
    score += (formatScore * SCORING_WEIGHTS.ATS_FORMAT) / 100;

    // Structure
    const structureScore = this.assessStructure(resumeText);
    score += (structureScore * SCORING_WEIGHTS.CONTENT_STRUCTURE) / 100;

    // Contact info
    const contactScore = this.assessContactInfo(resumeText);
    score += (contactScore * SCORING_WEIGHTS.CONTACT_INFO) / 100;

    // Measurable results
    const resultsScore = this.assessMeasurableResults(resumeText);
    score += (resultsScore * SCORING_WEIGHTS.MEASURABLE_RESULTS) / 100;

    return Math.min(Math.round(score), 100);
  }

  assessFormat(text) {
    let score = 0;
    const wordCount = (text.match(/\S+/g) || []).length;

    if (wordCount >= 300 && wordCount <= 1000) score += 30;
    else if (wordCount >= 200 && wordCount <= 1500) score += 20;
    else score += 10;

    if (/\b(20\d{2}|19\d{2})\b/.test(text)) score += 20; // years present
    if (/[\-\*]\s/.test(text)) score += 20; // bullets
    if (text.includes('\n')) score += 15; // multi-line
    const specials = (text.match(/[^\w\s.,;:()\-]/g) || []).length / Math.max(text.length, 1);
    if (specials < 0.05) score += 15;

    return Math.min(score, 100);
  }

  assessStructure(text) {
    let score = 0;
    const t = text.toLowerCase();

    if (/experience|employment|work history/.test(t)) score += 25;
    if (/education|academic/.test(t)) score += 25;
    if (/skills|competencies|expertise/.test(t)) score += 25;
    if (/summary|objective|profile/.test(t)) score += 15;

    const headers = t.match(/\n[ a-z/,&-]{3,30}\n/g) || [];
    if (headers.length >= 3) score += 10;

    return Math.min(score, 100);
  }

  assessContactInfo(text) {
    let score = 0;
    if (/@[\w.-]+\.\w{2,}/.test(text)) score += 35;
    if (/\d{3}[-.\s]?\d{3}[-.\s]?\d{4}/.test(text)) score += 35;
    if (/linkedin\.com/i.test(text)) score += 15;
    if (/github\.com|portfolio|website|blog/i.test(text)) score += 15;
    return Math.min(score, 100);
  }

  assessMeasurableResults(text) {
    let score = 0;
    const numbers = text.match(/\b\d+(\.\d+)?%?\b/g) || [];
    if (numbers.length >= 5) score += 40;
    else if (numbers.length >= 3) score += 25;
    else if (numbers.length >= 1) score += 15;

    const verbs = [
      'achieved','improved','increased','decreased','reduced','managed',
      'led','developed','implemented','created','designed','optimized',
    ];
    const found = verbs.filter((v) => new RegExp(`\\b${v}\\b`, 'i').test(text)).length;
    score += Math.min(found * 10, 60);
    return Math.min(score, 100);
  }

  calculateScoreBreakdown(resumeText, matchedKeywords, allKeywordsLower) {
    const keywordsPct =
      allKeywordsLower.length > 0
        ? Math.round((matchedKeywords.length / allKeywordsLower.length) * 100)
        : 0;

    return {
      keywords: keywordsPct,
      formatting: this.assessFormat(resumeText),
      structure: this.assessStructure(resumeText),
      contact: this.assessContactInfo(resumeText),
      results: this.assessMeasurableResults(resumeText),
    };
  }

  generateRecommendations(resumeText, missingKeywordsLower, atsScore) {
    const recs = [];
    const title = (s) => s.charAt(0).toUpperCase() + s.slice(1);

    if (missingKeywordsLower.length > 0) {
      const top = missingKeywordsLower.slice(0, 5).map(title);
      recs.push({
        priority: 'high',
        category: 'keywords',
        title: 'Add Missing Keywords',
        description: `Incorporate these important keywords: ${top.join(', ')}.`,
        impact: 'High  significantly improves ATS compatibility.',
      });
    }

    if (atsScore < 70) {
      recs.push({
        priority: 'high',
        category: 'structure',
        title: 'Improve Resume Structure',
        description: 'Use clear section headers: Summary, Experience, Education, Skills.',
        impact: 'High  improves ATS parsing and readability.',
      });
    }

    if (!/@[\w.-]+\.\w{2,}/.test(resumeText)) {
      recs.push({
        priority: 'high',
        category: 'formatting',
        title: 'Add Contact Information',
        description: 'Include an email and phone number at the top of your resume.',
        impact: 'Critical  required for employers to reach you.',
      });
    }

    const numbers = (resumeText.match(/\b\d+(\.\d+)?%?\b/g) || []).length;
    if (numbers < 3) {
      recs.push({
        priority: 'medium',
        category: 'content',
        title: 'Add Quantifiable Achievements',
        description: 'Use numbers/percentages (e.g., Increased uptime by 25%).',
        impact: 'Medium  makes impact concrete.',
      });
    }

    recs.push({
      priority: 'medium',
      category: 'content',
      title: 'Use Strong Action Verbs',
      description: 'Start bullets with Managed, Developed, Optimized, etc.',
      impact: 'Medium  sharper, outcome-focused bullets.',
    });

    if (!/linkedin\.com/i.test(resumeText)) {
      recs.push({
        priority: 'low',
        category: 'formatting',
        title: 'Add LinkedIn Profile',
        description: 'Include your LinkedIn URL for added context.',
        impact: 'Low  helpful for recruiters.',
      });
    }

    return recs;
  }

  generateInsights(resumeText, matchedKeywordsLower, missingKeywordsLower) {
    const wordCount = (resumeText.match(/\S+/g) || []).length;
    const total = matchedKeywordsLower.length + missingKeywordsLower.length;
    const matchRate = total > 0 ? (matchedKeywordsLower.length / total) * 100 : 0;

    return [
      {
        label: 'Keyword Match Rate',
        value: `${matchRate.toFixed(1)}%`,
        description: 'Percent of job keywords present in your resume.',
        status: matchRate >= 70 ? 'excellent' : matchRate >= 50 ? 'good' : 'needs-work',
      },
      {
        label: 'Resume Length',
        value: `${wordCount} words`,
        description: 'Ideal range is roughly 400800 words.',
        status: wordCount >= 400 && wordCount <= 800 ? 'excellent'
              : wordCount >= 300 && wordCount <= 1000 ? 'good'
              : 'needs-work',
      },
      {
        label: 'Skills Alignment',
        value: matchedKeywordsLower.length > 10 ? 'Strong'
              : matchedKeywordsLower.length > 5 ? 'Moderate'
              : 'Weak',
        description: 'How well listed skills align with the role.',
        status: matchedKeywordsLower.length > 10 ? 'excellent'
              : matchedKeywordsLower.length > 5 ? 'good'
              : 'needs-work',
      },
      {
        label: 'ATS Readability',
        value: /[\-]\s/.test(resumeText) ? 'Good' : 'Needs Work',
        description: 'Use bullet points and simple formatting.',
        status: /[\-]\s/.test(resumeText) ? 'good' : 'needs-work',
      },
    ];
  }

  extractMetadata(text) {
    return {
      hasEmail: /@[\w.-]+\.\w+/.test(text),
      hasPhone: /\d{3}[-.\s]?\d{3}[-.\s]?\d{4}/.test(text),
      hasLinkedIn: /linkedin\.com/i.test(text),
      hasGitHub: /github\.com/i.test(text),
      hasSections: {
        experience: /experience/i.test(text),
        education: /education/i.test(text),
        skills: /skills/i.test(text),
        summary: /summary|objective|profile/i.test(text),
      },
      wordCount: (text.match(/\S+/g) || []).length,
      hasYears: /\b(20\d{2}|19\d{2})\b/.test(text),
      hasBulletPoints: /[\-\*]\s/.test(text),
      estimatedSections: this.countSections(text),
    };
  }

  countSections(text) {
    const patterns = [
      /experience|employment|work history/i,
      /education|academic/i,
      /skills|competencies|expertise/i,
      /summary|objective|profile/i,
      /certifications|licenses/i,
      /projects/i,
      /awards|achievements/i,
    ];
    return patterns.filter((re) => re.test(text)).length;
  }

  /**
   * Merge traditional + AI analyses (AI optional)
   */
  mergeAnalyses(traditional, ai) {
    if (!ai) return { ...traditional, aiEnhanced: false, analysisType: 'traditional' };

    const uniq = (arr) => [...new Set(arr)];
    const mergedKeywords = {
      matched: uniq([
        ...traditional.keywords.matched,
        ...(ai.keywordAnalysis?.matched || []),
      ]),
      missing: uniq([
        ...traditional.keywords.missing,
        ...(ai.keywordAnalysis?.missing || []),
      ]),
      total: traditional.keywords.total,
    };

    const mergedScores = {
      overall: ai.atsScore?.overall ?? traditional.scores.overall,
      keywordMatch: ai.keywordAnalysis?.relevanceScore ?? traditional.scores.keywordMatch,
      breakdown: ai.atsScore?.breakdown ?? traditional.scores.breakdown,
    };

    return {
      scores: mergedScores,
      keywords: mergedKeywords,
      recommendations: [
        ...traditional.recommendations,
        ...(ai.recommendations || []),
      ],
      insights: traditional.insights,
      strengths: ai.strengths || [],
      weaknesses: ai.weaknesses || [],
      aiSuggestions: ai.aiEnhancedSuggestions || [],
      metadata: traditional.metadata,
      analysisType: 'hybrid',
      aiEnhanced: true,
    };
  }
}

// Export singleton
export const analysisService = new AnalysisService();
export default AnalysisService;
