// src/main.jsx
import React from 'react';
import ReactDOM from 'react-dom/client';
import { HashRouter } from 'react-router-dom';
import App from './App';
import './styles/index.css';

//  Entry point for SmartResumeTool
// Using HashRouter ensures navigation works correctly on GitHub Pages.
// (BrowserRouter would cause 404 errors on page refresh because GitHub Pages lacks server-side routing.)

const rootElement = document.getElementById('root');

if (rootElement) {
  const root = ReactDOM.createRoot(rootElement);
  root.render(
    <React.StrictMode>
      <HashRouter>
        <App />
      </HashRouter>
    </React.StrictMode>
  );
} else {
  console.error(' Root element not found! Check your index.html for <div id="root"></div>');
}
