// src/services/claudeAPI.js
/**
 * Claude API Service (Proxy-only)
 * The browser only calls your Cloudflare Worker (VITE_API_BASE_URL).
 * No API key is ever present in the client bundle.
 */

const BASE_URL =
  import.meta.env.VITE_API_BASE_URL ||
  'https://smart-resume-proxy.davidmadison95.workers.dev';

const MODEL =
  import.meta.env.VITE_CLAUDE_MODEL || 'claude-3-5-sonnet-20241022';

const MAX_TOKENS = Number(import.meta.env.VITE_CLAUDE_MAX_TOKENS) || 4096;
const TEMPERATURE = Number(import.meta.env.VITE_CLAUDE_TEMPERATURE) || 0.3;

class ClaudeAPIService {
  // Always configured because we use the proxy endpoint
  isConfigured() {
    return Boolean(BASE_URL);
  }

  async _post(path, body) {
    const res = await fetch(`${BASE_URL}${path}`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(body),
    });

    // Surface proxy / upstream errors cleanly
    if (!res.ok) {
      const text = await res.text().catch(() => '');
      throw new Error(text || `Proxy request failed (${res.status})`);
    }
    return res.json();
  }

  async sendMessage(prompt, options = {}) {
    const payload = {
      model: MODEL,
      max_tokens: options.maxTokens ?? MAX_TOKENS,
      temperature: options.temperature ?? TEMPERATURE,
      messages: [{ role: 'user', content: prompt }],
    };

    const data = await this._post('/v1/messages', payload);

    // Anthropic messages API returns { content: [ { type:'text', text:'...' } ] }
    const first = data?.content?.[0]?.text ?? '';
    if (!first) throw new Error('Empty response from model.');
    return first;
  }

  async analyzeResume(resumeText, jobDescription) {
    const prompt = `You are an expert resume analyzer and career coach. Analyze the following resume against the job description and provide detailed insights.

RESUME:
${resumeText}

JOB DESCRIPTION:
${jobDescription}

Please provide a comprehensive analysis in the following JSON format:
{
  "keywordAnalysis": {
    "matched": ["keyword1", "keyword2"],
    "missing": ["keyword1", "keyword2"],
    "relevanceScore": 0
  },
  "atsScore": {
    "overall": 0,
    "breakdown": { "formatting": 0, "keywords": 0, "structure": 0, "contact": 0 }
  },
  "strengths": ["..."],
  "weaknesses": ["..."],
  "recommendations": [
    { "priority": "high", "category": "keywords", "title": "Add ...", "description": "..." }
  ],
  "aiEnhancedSuggestions": [
    { "type": "bullet", "original": "...", "improved": "...", "explanation": "..." }
  ]
}

Return ONLY valid JSON.`;

    const text = await this.sendMessage(prompt, {
      maxTokens: MAX_TOKENS,
      temperature: 0.3,
    });

    // Strip accidental code fences & parse JSON
    const cleaned = text.replace(/```json\s*|\s*```/g, '').trim();
    return JSON.parse(cleaned);
  }

  async enhanceBulletPoints(originalBullets, jobKeywords) {
    const prompt = `Enhance these resume bullets with relevant keywords and quantifiable results.

BULLETS:
${originalBullets.join('\n')}

KEYWORDS: ${jobKeywords.join(', ')}

Return JSON array:
[
  { "original": "...", "enhanced": "...", "keywords_added": ["..."] }
]`;

    const text = await this.sendMessage(prompt, { temperature: 0.7 });
    const cleaned = text.replace(/```json\s*|\s*```/g, '').trim();
    return JSON.parse(cleaned);
  }

  async generateSummary(resumeText, jobDescription) {
    const prompt = `Create a 34 sentence professional summary that matches this job:

RESUME:
${resumeText.slice(0, 2000)}

JOB DESCRIPTION:
${jobDescription}

Return only the summary text.`;

    return this.sendMessage(prompt, { maxTokens: 500, temperature: 0.7 });
  }
}

export const claudeAPI = new ClaudeAPIService();
export default ClaudeAPIService;
